.alerta {
  padding: 0.5rem;
  text-align: center;
  color: white;
  text-transform: uppercase;
  margin: 1rem 0;
  border-radius: 20px;
}
.alerta.error {
  background-color: #f72121;
}
.alerta.exito {
  background-color: green;
  text-align: center;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: green;
}
table.propiedades thead th {
  color: white;
  padding: 2rem;
}
table.propiedades tr td {
  color: white;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.imagen-miniatura {
  width: 20rem;
}